import React from 'react';
import Divider from '@material-ui/core/Divider';
import { makeStyles } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import Routes from './Routes';
import { Link, DirectLink, Element, Events, animateScroll as scroll, scrollSpy, scroller } from 'react-scroll';
import MenuItem from '@material-ui/core/MenuItem';
import MenuList from '@material-ui/core/MenuList';
import logofav from "../assets/LogoFav.png"
import styled from 'styled-components';
import { createMuiTheme } from '@material-ui/core/styles';
import logo from '../assets/Logo.png';

const lam = createMuiTheme({
  palette: {
    primary: {
      // light: will be calculated from palette.primary.main,
      main: '#FAD961',
      // dark: will be calculated from palette.primary.main,
      // contrastText: will be calculated to contrast with palette.primary.main
    },
  }
});

const ImageOverlay = styled.div`
    @media all and (pointer: coarse) {
    background:lam.palette.primary.main,   
}
`;


const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
  },
  list: {
    width: 250,
  },
  // menuButton: {
  //   // marginRight: theme.spacing(-3),
  // },
  // edgeEnd:{
  //   marginLeft:-40,
  //   '$sizeSmall&': {
  //     marginLeft: -1,
  //   },
  // },
  title: {
    flexGrow: 1,
    paddingLeft:-20,
    // marginLeft:-20,
    '$sizeSmall&': {
      marginLeft: 2,
    },
  },
  edgeEnd: {
    // paddingRight:-12,
    
  },
  toolbarButtons: {
    marginLeft: 'auto',
    marginRight: -12,
    '$sizeSmall&': {
      marginRight: -3,
    },
  },
}));


function Navbar(props) {

  const [state, setState] = React.useState({
    left: false,
    right: false,
  });

  const toggleDrawer = (side, open) => event => {
    if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    setState({ ...state, [side]: open });
  };


  const sideList = side => (
    <div
      className={classes.list}
      role="presentation"
      onClick={toggleDrawer(side, false)}
      onKeyDown={toggleDrawer(side, false)}
    >
      
      <MenuList>
     <div className="text-center py-2"> <img src={logo} height="120vh" width="150vw"></img>  </div>
      <div className="py-2"><Divider/></div>
        {Routes.map((prop, index) => (
      <MenuItem component={Link} to={prop.id} spy={true} smooth={true} duration={500} className="py-2"><prop.icon class="py-2" size="3.5rem" color=" orange" class="py-3"></prop.icon><h4>{prop.sidebarName}</h4></MenuItem>
        ))}
      </MenuList>
    </div>
  );

  const classes = useStyles();

  return (
    <div className={classes.root}>
      <ImageOverlay>
      <AppBar position="relative" style={{backgroundColor:"#ff9a44"}}>
        <Toolbar>
          {/* <IconButton edge="end" className={classes.menuButton ,classes.edgeStart} color="inherit" aria-label="menu"> */}
          {/* <IconButton edge="end" className={classes.menuButton, classes.edgeEnd } color="inherit" aria-label="menu">
            <Button onClick={toggleDrawer('right', true)}><MenuIcon/></Button>
          </IconButton> */}
         <img src={logofav} height="60vh" width="100vw"></img>
          <div className={classes.toolbarButtons}>
          <IconButton edge="end"  color="inherit" aria-label="menu">
            <Button onClick={toggleDrawer('right', true)}><MenuIcon/></Button>
          </IconButton>
         </div>
          <Drawer anchor="right" open={state.right} onClose={toggleDrawer('right', false)}>
            {sideList('right')}
          </Drawer>
          
          {/* <Button>Login</Button>
          <Button>Signin</Button> */}
        </Toolbar>
      </AppBar>
      </ImageOverlay>
    </div>
  );
}

export default Navbar
